/* eslint-disable */
let hostPort = null;
let clientPort = [];
const HOST = 0,
	CLIENT = 1;
let count = 0;
onconnect = function (e) {
	const port = e.ports[0];
	port.onmessage = e => {
		const [method, data] = e.data;
		switch (method) {
			case "close":
				if (hostPort === port) {
					hostPort = null;
				} else {
					clientPort = clientPort.filter(port => port !== port);
				}
				break;
			case "register":
				count++;
				if (data === HOST) hostPort = port;
				if (data === CLIENT) clientPort.push(port);
				break;
			case "dispatch":
			case "commit":
				if (hostPort) hostPort.postMessage(e.data);
				break;
			case "stateUpdate":
				clientPort.forEach(port => {
					port.postMessage(e.data);
				});
		}
		port.postMessage(["success!", count]);
	};
};
